/* stylelint-disable color-no-hex */
$white: #fff;
$black: #1c1d22;
$black-o: rgba(0, 0, 0, 0.6);

.uxAside--theme-light {

  .uxAside-header-icon {
    color: $black-o;
  }

  .uxAside-header-title {
    color: $black;
  }

  .uxAside-header-subtitle {
    color: $black-o;
  }

  .uxAside-button-close {
    background: url('data:image/png;base64,') no-repeat 50% 50%;
  }

  .uxAside-button-fullscreen {
    background: url('data:image/png;base64,') no-repeat 50% 50%;
  }

  &.isFullscreen .uxAside-button-fullscreen {
    background: url('data:image/png;base64,') no-repeat 50% 50%;
  }
}
